import {Component} from "react"
import React from "react"

class Juridique extends Component {
    render() {
        return (
            <div>
              <h2>Entits Juridiques</h2>
              <p>Lorganisation juridique du Village n1 ENTREPRISES est subdivise en plusieurs entits : une associations sans but lucratif et trois socits anonymes  finalit sociale. Chaque entit a ses spcificits mais elles travaillent toutes  des objectifs similaires et convergents.
              <br/>
              Village n1 ENTREPRISES est une organisation unique qui dfend des valeurs communes et assure des missions homognes.</p>
              <h3>Une ASBL</h3>
              <ul>
                <li>
                ETA Village n1 asbl
                L'asbl ETA Village n1 a pour objets l'occupation, la formation et la mise au travail de personnes adultes ayant un handicap reconnu par un pouvoir subsidiant et de personnes en difficults dintgration.
                </li>
              </ul>
              <h3>Les safs</h3>
              <p>Les socits anonymes  finalit sociale ont pour objectif linsertion des personnes handicapes ou socialement dfavorises, par la cration demplois stables et rmunrs et par lorganisation ventuelle des formations ncessaires.
              La socit vise la rinsertion sociale et professionnelle de personnes exclues des circuits traditionnels de lemploi et est active dans la lutte contre le chmage ou toute autre forme dexclusion.
              Lobjectif prioritaire des safs est la cration demploi durable, de qualit, et rmunrateur.
              Les safs de Village n1 ENTREPRISES: ADM Village n1, Bteam Village n1, Proxi Village n1.</p>
            </div>
        )
    }
}

export default Juridique;



