import React, {Component} from "react";
import {Route,Link} from 'react-router-dom';

class Taxi extends Component {
  render(){
    return(<div>
            <h1>Taxi social</h1>
            <strong>
             Le taxi social du Village n1, la solution  vos problmes de transport et d'accompagnement.  Appelez le 02/388.05.11
            </strong>
            <strong>
             Services proposs:
            </strong>
            <ul>
              <ol>
                Un service de transport dans un rayon de 30km autour de Braine-l'Alleud (except Bruxelles)
              </ol>
              <ol>
                Un service d'accompagnement personnalis
              </ol>
            </ul>
            <strong>
              <ul>
                <ol>
                le service est destin en priorit aux personnes qui disposent d'un faible revenu, aux personnes handicapes ou  mobilit rduite et aux plus de 65 ans
                </ol>
                <ol>
                  le tarif du transport est de 0,3595 par km parcouru
                </ol>
                <ol>
                inscription pralable
                </ol>
                <ol>
                paiement par carte d'abonnement
                </ol>
                <ol>
                numro d'appel: 02/388 05 11
                </ol>
                <ol>
                rservation 48h  l'avance
                </ol>
              </ul>
            </strong>
            <p>
            Cette initiative a abouti grce  une collaboration troite avec le CPAS de Braine-l'Alleud et la commune de Braine-le-Chteau. Nous remercions galement le Lions Club de Braine-l'Alleud et l'cole Peugeot de matrise automobile pour leur soutien dans ce projet.
            </p>
           </div>
         );
  }
}

export default Taxi ;
const container = {
    maxWidth:'600px',
}

const imgContainer =
{
    height:'337px',
    overflow:'hidden'
}

const imageStyle = {
    width: '120%',
    transform: 'translateX(-10%)',
    borderRadius: '3px'
}



